import axios from 'axios';
import { getAccessToken } from '../api/auth';

const API_BASE_URL = 'https://sellingpartnerapi-eu.amazon.com';
const CLIENT_ID = process.env.CLIENT_ID;
const APP_NAME = 'amzn1.sp.solution.e1d187dc-f121-4666-9ebf-6ebc13a1f123';

export const fetchOrders = async () => {
  const token = await getAccessToken();
  try {
    const response = await axios.get(`${API_BASE_URL}/orders/v0/orders`, {
      headers: {
        'x-amz-access-token': token,
        'x-amz-client-id': CLIENT_ID,
        'x-amz-app-name': APP_NAME,
      },
    });
    return response.data.orders;
  } catch (error) {
    console.error('Error fetching orders:', error);
    throw error;
  }
};

export const updateOrderStatus = async (orderId) => {
  const token = await getAccessToken();
  try {
    const response = await axios.patch(`${API_BASE_URL}/orders/v0/orders/${orderId}/status`, {
      status: 'Sended',
    }, {
      headers: {
        'x-amz-access-token': token,
        'x-amz-client-id': CLIENT_ID,
        'x-amz-app-name': APP_NAME,
      },
    });
    return response.data;
  } catch (error) {
    console.error('Error updating order status:', error);
    throw error;
  }
};
