<template>
  <div>
    <h1>Amazon Orders</h1>
    <ul>
      <li v-for="order in orders" :key="order.amazonOrderId">
        <p>Name: {{ order.name }}</p>
        <p>Status: {{ order.status }}</p>
        <p>Amazon Order ID: {{ order.amazonOrderId }}</p>
        <p>Shipping Information: {{ order.shippingInfo }}</p>
        <button @click="markAsSended(order.amazonOrderId)">Sended</button>
      </li>
    </ul>
  </div>
</template>

<script>
import { fetchOrders, updateOrderStatus } from '@/services/amazonService';

export default {
  data() {
    return {
      orders: [],
      token: 'your-access-token', // Replace with actual token
    };
  },
  async created() {
    this.orders = await fetchOrders(this.token);
  },
  methods: {
    async markAsSended(orderId) {
      await updateOrderStatus(orderId, this.token);
      this.orders = await fetchOrders(this.token); // Refresh the order list
    },
  },
};
</script>

